.img-box-road {
    height: 749px; }


.road-img {
    object-fit: none;
    max-width: 100%;
    height: 100%;

    position: relative;
    margin-top: 40px; }

.news-slogan {
    position: absolute;
    font-weight: 700;
    top: 1260px;
    right: 850px;
    background: rgba(255, 255, 255, 0.6 );
    padding: 10px 35px;
    color: #282828;
    @include media(411px) {
        right: 22px;
        padding: 10px 20px;
        top: 1290px; }
    @include media(768px) {
        right: 170px;
        font-size: 24px;
        line-height: 34px;
        top: 1220px; }
    @include media(1024px) {
        font-size: 30px;
        padding: 15px 20px;
        right: 300px;
        top: 1200px;
        @include media(1280px) {
            right: 410px;
            font-size: 36px;
            width: 800px;
            top: 1160px; }
        @include media(1350px) {
            right: 500px; }
        @include media(1920px) {
            right: 900px;
            top: 1150px; } } }
