
@import './../mixins/_Mixins';

.history-group {
    padding: 0px 52px 70px;
    margin-top: 70px; }
.img-his__group {
    padding: 0px 55px; }
.history-title {
    font-weight: 700;
    font-size: 48px;
    line-height: 65px;
    color: #282828;
    text-align: center;
    margin-top: 70px; }

.history-img {
    height: 674px; }
.his-banner {
    object-fit: none;
    max-width: 100%;
    height: 100%;
    position: relative;
    margin: auto;
    display: block;
    text-align: center; }

.his-text {
    font-weight: 600;
    font-size: 18px;
    line-height: 24px;
    color: #282828;
 }    // margin-bottom: 50px

.img-list__box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 60px; }
.img-his__item {
    height: 540px; }
.img__his {
    object-fit: none;
    // max-width: 100%
    height: 100%;
    position: relative;
    width: 720px; }



