.mobil {
    // visibility: hidden
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 100;
    background: #357ECB;
    // background-color: rgba(0,0,0,0.9)
    padding: 20px 20px 55px;
    overflow: auto; }
.tel {
    visibility: hidden;
    opacity: 0;
    height: 0; }
.mobil-mail {
    display: flex;
    justify-content: space-between; }

.menu__list {
    margin-bottom: 0;
    display: block; }
.mail__title {
    font-weight: 300;
    font-size: 13px;
    line-height: 14px;
    color: #fff; }
.form__search-mobil {
    position: relative;
    margin-top: 5px; }

