
@import './../mixins/_Mixins';
.header-news {
    background: #357ECB; }

.header-box {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0px;
    @include media_max(1023px) {
        display: none; }
    // +media_max(1920px)
 }    //     display: block

.header-news--left {
    display: flex; }





.news-clock {
    font-family: 'Roboto Condensed';
    font-weight: 400;
    font-size: 18px;
    color: #FFFFFF;
    line-height: 21px;
    margin-left: 4px; }


.news-clock:hover {
    color: red; }


.header-moon {
    margin-left: 20px; }

.header-moon:hover {
    color: red; }

.header-news__link {
    font-family: 'Roboto Condensed';
    font-weight: 400;
    font-size: 18px;
    color: #FFFFFF;
    text-decoration: none; }

.news__link {
    display: block;
    padding: 7px 28px; }

.header-news--right {
    display: flex; }




.header-news__link:hover {
    background: #fff;
    color: #357ECB;
    transition: 0.5s; }
.news-info {
    display: flex; }

.news-kg {
    font-family: 'Roboto Condensed';
    font-weight: 400;
    font-size: 18px;
    color: #FFFFFF;
    text-decoration: none;
    margin-left: 5px;
    transition: 0.5s; }
.news-kg:hover {
    color: red; }
.news-kg--decor {
    margin-left: 4px;
    color: #fff; }
